import React from 'react'
import styled from 'styled-components'
import { AllocationData } from '.'

const StyledAllocationArea = styled.div`
  padding: 13px 0;
`

interface AllocationProps {
  netWorth: number
  data: AllocationData[]
}

const Allocation: React.FC<AllocationProps> = ({ netWorth, data }) => {
  const sortedData = data.sort((a, b) => a.worth - b.worth)

  return (
    <div className="col-md-6 col-lg-6 col-12">
      <div className="spacel">
        <h4>Asset Allocation</h4>
        {sortedData.map((data) => {
          const percent = data.worth / netWorth * 100
          return (
            <StyledAllocationArea>
              <div className="flex justify-between text-16">
                <div className="inline-flex items-baseline font-700">
                  {data.currency.name}
                </div>
                <span className="text-dark-64">{`${percent}%`}</span>
              </div>
              <div className="relative rounded-full bg-linen mt-8 h-10 overflow-hidden">
                <div className="absolute h-10 bg-pacoca" style={{width: `${percent}%`}} />
              </div>
            </StyledAllocationArea>
          )
        })}
      </div>
    </div>
  )
}

export default Allocation
