import React, { useState } from 'react'
import { Currency } from '@uniswap/sdk-core'
import Wallet from './Wallet'
import Allocation from './Allocation'

interface WalletAndAllocationProps {
  currencyList: Currency[]
  changeWorth: (tokenWorth: number) => void
  onCurrencySelect: (currency: Currency) => void
  netWorth: number
}

export interface AllocationData {
  currency: Currency
  worth: number
}

const WalletAndAllocation: React.FC<WalletAndAllocationProps> = ({ changeWorth, currencyList, onCurrencySelect, netWorth }) => {
  const [allocationDatas, setAllocationDatas] = useState<AllocationData[]>([])

  const handleAllocationDataChange = (data: AllocationData) => {
    console.log("pooh, data = ", data)
    if (data.worth) {
      const datas = allocationDatas
      let exist = false
      for (let i = 0; i < datas.length; i ++) {
        if (datas[i].currency.name === data.currency.name) {
          datas[i].worth = data.worth
          exist = true
          break
        }
      }
      if (!exist) {
        datas.push(data)
      }
      setAllocationDatas(datas)
    }
  }

  return (
    <section className="wallet_sec">
      <div className="container">
        <div className="col-md-11 col-lg-11 col-12 m-auto">
          <div className="row">
            <Wallet currencyList={currencyList} changeWorth={changeWorth} onCurrencySelect={onCurrencySelect} onAllocationDataChange={handleAllocationDataChange} />
            <Allocation netWorth={netWorth} data={allocationDatas} />
          </div>
        </div>
      </div>
    </section>
  )
}

export default WalletAndAllocation
